:root {
  --primary-color: #00acdc;
  --link-height: 60px;
  --active-border-height: 4px;
}

html, body, header, a, img, nav, ul, li {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  line-height: 0;
}

.logo-link:hover {
  text-decoration: none;
}

.nav {
  display: flex;
}

ul {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

li {
  list-style-type: none;
}

li + li {
  margin-left: 20px;
}

li:first-child {
  margin-left: 0;
}

li:last-child {
  margin-right: 0;
}

.nav a {
  color: #000;
  height: var(--link-height);
  align-items: center;
  margin-right: 20px;
  font-family: Roboto, sans-serif;
  text-decoration: none;
  display: inline-flex;
}

.nav a:hover {
  color: var(--primary-color);
}

.nav a:last-of-type {
  margin-right: 0;
}

.nav a.is-active {
  color: var(--primary-color);
  position: relative;
}

.nav a.is-active:after {
  content: "";
  left: 0;
  top: calc(var(--link-height)  - var(--active-border-height));
  height: var(--active-border-height);
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  position: absolute;
}
